@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.0.0/css/all.min.css');

body {
    background: linear-gradient(135deg, #2C3E50, #34495E); 
    color: #f1f1f1; 
    font-family: 'Arial', sans-serif;
}


ul {
    list-style: none;
    padding: 0;
}

li {
    margin-bottom: 20px;
    padding: 15px;
    background: #444;
    border: 1px solid #555;
    border-radius: 8px;
    color: #f1f1f1;
    transition: all 0.3s ease;
}

li:hover {
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    transform: translateY(-5px);
}

li button {
    background-color: #dc3545;
    color: white;
    border: none;
    padding: 8px 16px;
    border-radius: 8px;
    cursor: pointer;
    font-size: 14px;
    transition: background-color 0.3s ease;
}

li button:hover {
    background-color: #c82333;
}

.agenda-container {
    max-width: 600px;
    margin: 30px auto;
    padding: 30px;
    background: #222222;
    border-radius: 10px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
    font-family: 'Arial', sans-serif;
    color: #f1f1f1;
}

h2 {
    text-align: center;
    font-family: 'Arial', sans-serif;
    font-size: 24px;
    color: #e4e8ea;
    margin-bottom: 25px;
}

form label {
    display: block;
    margin-bottom: 10px;
    font-size: 16px;
    color: #f3f0f0;
}

input, select, textarea, button {
    width: 100%;
    padding: 12px;
    margin-top: 8px;
    border: 1px solid #555;
    border-radius: 8px;
    font-size: 16px;
    color: #333;
    background-color: #444;
    transition: all 0.3s ease;
}

input:focus, select:focus, textarea:focus {
    border-color: #ffcc00;
    outline: none;
    background-color: #555;
}

button {
    background-color: #007bff;
    color: white;
    border: none;
    cursor: pointer;
    font-size: 16px;
    margin-top: 20px;
    transition: background-color 0.3s ease;
}

button:hover {
    background-color: #0056b3;
}

.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal-box {
    background: #333;
    padding: 25px;
    border-radius: 10px;
    width: 450px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
    color: #f1f1f1;
}

.open-modal-button, .close-modal-button {
    margin-top: 15px;
    padding: 12px 25px;
    cursor: pointer;
    background-color: #37688f;
    color: white;
    border-radius: 8px;
    font-size: 16px;
    transition: background-color 0.3s ease;
}

.open-modal-button:hover, .close-modal-button:hover {
    background-color: #0068e6;
}

.agenda-container {
    max-width: 100%;
    margin: 30px auto;
    padding: 30px;
    background: #222222;
    border-radius: 10px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
    font-family: 'Arial', sans-serif;
    color: #f1f1f1;
    overflow-x: auto; 
}

.agenda-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 30px;
    table-layout: auto; 
}

.agenda-table th, .agenda-table td {
    border: 1px solid #555;
    padding: 12px;
    text-align: left;
    font-size: 16px;
    color: #f1f1f1;
    word-wrap: break-word; 
}

.agenda-table th {
    background: #333;
    color: #00ccff; 
    font-weight: bold;
    text-transform: uppercase; 
    letter-spacing: 1px; 
    text-align: center; 
}

.agenda-table tr:nth-child(even) {
    background-color: #444; 
}

.agenda-table tr:nth-child(odd) {
    background-color: #333; 
}

.agenda-table tr:hover {
    background: #555; 
    cursor: pointer; 
}

.agenda-table td {
    background-color: #444; 
    text-align: center;
}


@media (max-width: 768px) {
    .agenda-table th, .agenda-table td {
        font-size: 14px;
        padding: 10px;
    }

    .agenda-table th {
        font-size: 16px; 
    }

    .agenda-table {
        display: block;
        overflow-x: auto; 
    }

    .agenda-table th, .agenda-table td {
        display: block;
        width: 100%;
        padding: 8px;
    }

    .agenda-table tr {
        display: flex;
        flex-direction: column;
        margin-bottom: 15px; 
    }
}

@media (max-width: 480px) {
    .agenda-table th, .agenda-table td {
        font-size: 12px; 
        padding: 6px; 
    }

    .agenda-table th {
        font-size: 14px; 
    }
}

@media (max-width: 768px) {
    .agenda-container {
        padding: 20px;
        margin: 20px;
    }

    h2 {
        font-size: 20px;
    }

    input, select, textarea, button {
        padding: 10px;
        font-size: 14px;
    }

    .modal-box {
        width: 90%;
        padding: 15px;
    }

    .agenda-table {
        display: block;
        width: 100%;
    }

    .agenda-table th, .agenda-table td {
        display: block;
        width: 100%;
        padding: 10px;
    }

    .agenda-table tr {
        margin-bottom: 15px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }

    .agenda-table th {
        background: #333;
        color: #00ccff;
        font-weight: bold;
        text-align: center;
    }

    .agenda-table td {
        background: #444;
    }

    li button {
        padding: 6px 12px;
        font-size: 12px;
    }
}

@media (max-width: 480px) {
    h2 {
        font-size: 18px;
    }

    input, select, textarea, button {
        font-size: 14px;
        padding: 8px;
    }

    .agenda-table th, .agenda-table td {
        font-size: 12px;
        padding: 8px;
    }

    li button {
        padding: 5px 10px;
        font-size: 10px;
    }
}

