}

.footer {
    background-color: #222222;
    color: #f1f1f1;
    text-align: center;
    padding: 20px;
    font-size: 16px; 
    margin-top: 30px; 
    width: 100%;
}

.footer i {
    color: red;
}

@media (max-width: 768px) {
    .footer {
        font-size: 14px; 
        padding: 15px;  
    }

    .footer p {
        margin: 0;
        padding: 0;
    }
}

@media (max-width: 480px) {
    .footer {
